import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  NgZone,
  OnDestroy,
  Renderer2,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {Slider} from 'primeng/slider';
import {DomHandler} from 'primeng/dom';
import {NG_VALUE_ACCESSOR} from '@angular/forms';
import {AdaptRadarAngularCustomEventName, AdaptRadarAngularService} from '../common/radar/adapt-radar';


// TODO: Refactor this if possible
/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export const ADAPT_SLIDER_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => AdaptSliderComponent),
  multi: true
};

@Component({
  selector: 'adapt-slider',
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.is-vertical]': 'orientation === "vertical"'
  },
  templateUrl: './slider.component.html',
  providers: [ADAPT_SLIDER_VALUE_ACCESSOR, DomHandler],
  encapsulation: ViewEncapsulation.None
})

export class AdaptSliderComponent extends Slider implements AfterViewInit, OnDestroy {
  /**
   * When specified, allows one boundary value to be picked
   * @docs-default false
   */
  @Input() rangeHorizontal: boolean = false;

  /**
   * @docs-default false
   */
  @Input() tick: boolean = false;

  /**
   * Array of ticks to be displayed, will be justified
   * @docs-default
   */
  @Input() ticks: number[];

  /**
   * When specified, allows move between steps depending on click event. Depending on 'step' attr
   * @docs-default false
   */
  @Input() customStepChange: boolean = false;

  /**
   * When specified, allows format the value in tooltip
   * @docs-default
   */
  @Input() formatter: Function;

  @Input() adaptRadarDisableEventSending: boolean = false;

  @ViewChild('bar', {static: true}) bar: ElementRef;

  private _elementIntersectObserver: IntersectionObserver;

  constructor(public el: ElementRef,
              public domHandler: DomHandler,
              public renderer: Renderer2,
              ngZone: NgZone,
              cd: ChangeDetectorRef,
              private _adaptRadarService: AdaptRadarAngularService) {
    super(el, renderer, ngZone, cd);
  }

  ngAfterViewInit(): void {
    this._elementIntersectObserver = this._adaptRadarService.subscribeOnElementIntersect(this.adaptRadarDisableEventSending, this.el, AdaptRadarAngularCustomEventName.AdaptSlider);
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();

    if (this._elementIntersectObserver) {
      this._elementIntersectObserver.disconnect();
      this._elementIntersectObserver = null;
    }
  }

  moveBetweenStepRange(value: number): void {
    const CALCULATED_VALUE = Math.round(value / this.step) * this.step;

    this.updateValue(CALCULATED_VALUE);
    this.updateHandleValue();
  }

  handleStepChange(newValue: number, oldValue: number): void {
    const savedValue = newValue;
    const diff = (newValue - oldValue);

    if (diff < 0 && (diff * -1) >= this.step / 2) {
      newValue = oldValue - this.step;
      this.updateValue(newValue);
      this.updateHandleValue();

      if (this.customStepChange) {
        this.moveBetweenStepRange(savedValue);
      }
    } else if (diff > 0 && diff >= this.step / 2) {
      newValue = oldValue + this.step;
      this.updateValue(newValue);
      this.updateHandleValue();

      if (this.customStepChange) {
        this.moveBetweenStepRange(savedValue);
      }
    }
  }

  getTooltipValue(value: number): unknown | number {
    return this.formatter ? this.formatter(value) : value;
  }
}
